<template>
  <div style="font-size:15px; background-color:#f6f6f6;position:fixed;top:70px;bottom:0px;left:0px;right:0px;overflow-y:scroll;color:#000;">
    <div>
      <div class="watchBox flex">
        <label>换表类型</label>
        <div
          @click="show = true"
          class="boxType flex"
        >
          <p>{{ txetRight }}</p>
          <img
            src="../../assets/right.png"
            alt=""
          >
        </div>
      </div>
      <!-- 新表 -->
      <div class="watchBox flex">
        <label class="old">新表读数
          <img
            class="xing"
            src="../../assets/two/write.png"
            alt=""
          >
        </label>
        <input
          type="number"
          class="right"
          placeholder="请输入新表初始读数"
          v-model="new_num"
        ><span>度</span>
      </div>
      <!-- 旧表费用结算 -->
      <div
        class="oldwatch"
        v-if="unit > 0"
      >
        <div class="watchBox w_left">
          <label class="old">旧表费用结算</label>
          <label class="c88">单位：&yen;{{ unit }}/吨</label>
        </div>
        <div class="bgf4">
          <div class="flex">
            <p
              v-for="item in three"
              :key='item.id'
            >{{ item.title }}</p>
          </div>
        </div>
        <div>
          <div class="watchBox flex w100">
            <p class="w20 w_left">{{ txetRight }}</p>
            <input
              type="text"
              class="w20"
              disabled
              v-if="value == 1"
              v-model="lastInfo.preElecDegree"
            >
            <input
              type="text"
              class="w20"
              disabled
              v-if="value == 2"
              v-model="lastInfo.preWaterDegree"
            >
            <input
              type="text"
              class="w20"
              disabled
              v-if="value == 3"
              v-model="lastInfo.preGasDegree"
            >
            <input
              type="number"
              class="w20 w_right"
              v-if="value == 1"
              v-model="cEle"
            >
            <input
              type="number"
              class="w20 w_right"
              v-if="value == 2"
              v-model="cWater"
            >
            <input
              type="number"
              class="w20 w_right"
              v-if="value == 3"
              v-model="cGas"
            >
          </div>
        </div>
        <div
          class="watchBox w_left p10"
          style="padding:10px 20px;"
        >
          <label class="old">费用总计</label>
          <label class="c88">&yen;{{ totlePrice?totlePrice.toFixed(2):0 }}</label>
        </div>
      </div>
      <mt-button
        id="button"
        @click="changeWatch"
      >确认换表</mt-button>
      <!-- 下发短信-修改密码 -->
      <mt-popup
        v-model="show"
        position="bottom"
        class="show_box"
      >
        <p class="title">选择换表类型</p>
        <div
          class="text"
          v-for="item in listType"
          :key="item.value"
        >
          <div @click="chooseType(item)">{{ item.text }}</div>
        </div>
      </mt-popup>
    </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessLifeCostBillLastByorderroom, PostBusinessOrderroomMeterChange } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            listType: [],
            value: null,
            show: false,
            txetRight: null,
            new_num: null,
            unit: '',
            cEle: '',
            cWater: '',
            cGas: '',
            three: [{ id: 0, title: '费用项目' }, { id: 1, title: '上期读数' }, { id: 2, title: '结算读数' }],
            lastInfo: ''
        };
    },
    created() {
        this.$store.commit('setHeader', '换表');
        this.id = this.$route.query.id;
        this.orderId = this.$route.query.orderId;
        this.types();
    },
    mounted() {},
    computed: {
        totlePrice() {
            if (this.value == 1 && this.cEle) {
                return Number(this.lastInfo.elecPrice) * (Number(this.cEle) - Number(this.lastInfo.preElecDegree));
            } else if (this.value == 2 && this.cWater) {
                return Number(this.lastInfo.waterPrice) * (Number(this.cWater) - Number(this.lastInfo.preWaterDegree));
            } else if (this.value == 3 && this.cGas) {
                return Number(this.lastInfo.gasPrice) * (Number(this.cGas) - Number(this.lastInfo.preGasDegree));
            }
        }
    },
    methods: {
        types() {
            GetSystemDictDataByType({ dictType: 'business_meter_type' }).done((res) => {
                let listType = [];
                for (let i = 0; i < res.data.length; i++) {
                    let obj = {};
                    obj['value'] = res.data[i].dictValue;
                    obj['text'] = res.data[i].dictLabel;
                    listType.push(obj);
                }
                this.listType = listType;
                this.txetRight = this.listType[0].text;
                this.value = this.listType[0].value;
                this.getLifeInfo();
            });
        },
        chooseType(type) {
            this.txetRight = type.text;
            this.value = type.value;
            this.getLifeInfo();
            this.show = false;
        },
        // 旧表费用结算
        getLifeInfo() {
            GetBusinessLifeCostBillLastByorderroom(this.id).done((res) => {
                this.lastInfo = res.data;
                if (this.value == 1) {
                    this.unit = this.lastInfo.elecPrice;
                } else if (this.value == 2) {
                    this.unit = this.lastInfo.waterPrice;
                } else {
                    this.unit = this.lastInfo.gasPrice;
                }
            });
        },
        // 确认换表
        changeWatch() {
            if (!this.new_num) {
                Toast({
                    message: '请输入新表读数',
                    duration: 900
                });
                return false;
            }
            if (this.unit) {
                if ((this.value == 1 && !this.cEle) || (this.value == 1 && this.cEle < this.lastInfo.preElecDegree)) {
                    Toast({
                        message: '请正确输入电表结算读数',
                        duration: 900
                    });
                    this.cEle = '';
                    return false;
                } else if ((this.value == 2 && !this.cWater) || (this.value == 2 && this.cWater < this.lastInfo.preWaterDegree)) {
                    Toast({
                        message: '请正确输入水表结算读数',
                        duration: 900
                    });
                    this.cWater = '';
                    return false;
                } else if ((this.value == 3 && !this.cGas) || (this.value == 3 && this.cGas < this.lastInfo.preGasDegree)) {
                    Toast({
                        message: '请正确输入气表结算读数',
                        duration: 900
                    });
                    this.cGas = '';
                    return false;
                }
            }
            if (this.value == 1) {
                this.data = {
                    orderId: this.orderId,
                    meterType: this.value,
                    preElecDegree: this.cEle,
                    elecDegree: this.new_num
                };
            } else if (this.value == 2) {
                this.data = {
                    orderId: this.orderId,
                    meterType: this.value,
                    preWaterDegree: this.cWater,
                    waterDegree: this.new_num
                };
            } else if (this.value == 3) {
                this.data = {
                    orderId: this.orderId,
                    meterType: this.value,
                    preGasDegree: this.cGas,
                    gasDegree: this.new_num
                };
            }
            PostBusinessOrderroomMeterChange(this.id, this.data).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                setTimeout('window.history.go(-1)', 900);
            });
        }
    }
};
</script>
<style scoped>
.flex {
    display: flex;
    justify-content: space-between;
    line-height: 30px;
    align-items: center;
}
.p10 {
    padding: 0 10px;
}
.w100 {
    width: 100%;
    box-sizing: border-box;
    padding: 0 20px;
}
.w20 {
    width: 30%;
    text-align: center;
    background: #fff;
}
.w_left {
    text-align: left;
    padding-left: 10px;
}
.w_right {
    border-left: 1px solid #e6e6e6;
    text-align: right;
    padding-right: 10px;
    color: #ff7d41;
}
.c88 {
    color: #888;
    padding-left: 8px;
}
.mt10 {
    margin: 0 10px;
}
.bgf4 {
    background-color: #f4f4f4;
    color: #9f9f9f;
    padding: 8px 22px;
}
.right {
    text-align: right;
    width: 70%;
}
.xing {
    width: 10px;
}
.watchBox {
    border-bottom: 1px solid #e6e6e6;
    background-color: #fff;
    padding: 10px;
}
.watchBox > .right {
    text-align: right;
}
.watchBox > .boxType > img {
    width: 20px;
    height: 20px;
}
.oldwatch {
    margin-top: 20px;
}
#button {
    background-color: #ff7d41;
    width: 90%;
    line-height: 30px;
    color: #fff;
    font-size: 0.9rem;
    margin-top: 50px;
}
/* 弹出框 */
.show_box {
    width: 100%;
}
.show_box > .title {
    padding: 10px;
    text-align: left;
}
.show_box > .text {
    /* border: 1px solid red; */
    padding: 10px 0;
    border-bottom: 1px solid #e6e6e6;
}
</style>



